import React from 'react';

//imports
import { Grommet } from 'grommet';
import { Routes, Route, Outlet, Link } from "react-router-dom";
import background from './assets/background_home.jpg'
//screens

import LoginSceen from './screens/login/login';
import HomeScreen from './screens/home/home';
import AdminScreen from './screens/admin/admin';
import { User } from 'grommet-icons';

function App() {
  const [logando, setLogado] = React.useState(true);
  const [user, setUser] = React.useState([]);
  const theme = {
    global: {
      font: {
        family: 'Poppins',
        size: '10px',
        height: '20px',
      },
      colors: {
        focus: '#0C85FF'
      }
    },

  };

  React.useEffect(() => { 
    // console.log(user)
   }, [user])


  return (
    <Grommet theme={theme}>

      {logando ? (<LoginSceen handleChange={() => setLogado(false)} setUser={(data) => setUser(data)} />) : (

        user.nivel === 1 ? (
          <AdminScreen />
        )

          :
          < HomeScreen handleChange={() => setLogado(true)} setUser={(data) => setUser(data)} />

      )
      }

    </Grommet >
  );

}

export default App;
