import React from 'react';

//Grommet
import { ResponsiveContext, Sidebar, Button, Nav, Text, Box, Header, TextInput, DataTable, Select, DateInput, Spinner } from 'grommet';
import { Home, BarChart, Clipboard, Group, SettingsOption, Search, Add } from 'grommet-icons';

//Mui
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack'; import Popover from '@mui/material/Popover';
import Avatar from '@mui/material/Avatar';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import TextField from '@mui/material/TextField';

//primreact
import { Sidebar as Sdbr } from 'primereact/sidebar';
//
import api from '../../services/api';
import logo from '../../assets/logo.jpg';



export const AdminScreen = ({ handleChange }) => {

    const size = React.useContext(ResponsiveContext);
    const [user, setUser] = React.useState('');
    const [loading, setLoading] = React.useState(true);
    const [serachInput, setSearchInput] = React.useState('');
    const [data, setData] = React.useState([]);
    const [recadacion, setRecadacion] = React.useState([]);
    const styles = {
        sidebar: {
            width: '30vh',
            height: '100vh',
        },
        navButtonActive: { color: 'blue', display: 'flex', alignContent: 'center', alignItems: 'center', alignContent: 'center' },
        bavButtonInactive: { color: 'black', display: 'flex', alignContent: 'center', alignItems: 'center', alignContent: 'center' }
    };

    function stringAvatar(name) {
        return {
            sx: {
                bgcolor: '#f25a2c',
            },
            children: `${name.split(' ')[0][0]}${name.split(' ')[1][0]}`,
        };
    }

    const [detallesVendas, setDetallesVendas] = React.useState([]);
    React.useEffect(() => {

        var datatime = new Date();
        var dia = String(datatime.getDate()).padStart(2, '0');
        var mes = String(datatime.getMonth() + 1).padStart(2, '0');
        var anoredux = String(datatime.getFullYear().toString().substr(-2));
        var ano = String(datatime.getFullYear());
        var fecha = ano + '-' + mes + '-' + dia;
        setData(fecha);
        setDesde(fecha);
        setHasta(fecha);
        // console.log(fecha);

        var fechaRedux = dia + '/' + mes + '/' + anoredux;

        let token = localStorage.getItem("token");
        let config = {
            headers: {
                Authorization: 'Bearer ' + token
            }
        }

        let data = {

        }

        api.post('/me', data, config).then((response) => {

            if (response.data.message === 'sucess') {

                api.get('/ventas-useeffect/' + fechaRedux, config).then((res) => {
                    setValReca(res.data[0].venta);
                    setCaja(res.data[0]);
                    setRecadacion(res.data);
                    setLoading(false)
                })

                api.post('/ventas-list-detalles', { fechaDesde: fechaRedux, fechaHasta: fechaRedux }, config).then((res) => {
                    setDetallesVendas(res.data);
                })
                setUser(response.data.usuario.original)

                return 1;
            }
            else {
                localStorage.clear();

            }

        })
            .catch((error) => console.log(error))
    }, [])

    const [caja, setCaja] = React.useState([]);
    const [visibleSidebar, setVisibleSidebar] = React.useState(false);
    const [valuSaldoAdd, setSaldoAdd] = React.useState('');

    const [valueReca, setValReca] = React.useState('Venta 1');

    const handleSelectRecadacion = (nextValue) => {
        let index = recadacion.findIndex(object => {
            return object.venta === nextValue;
        });
        setCaja(recadacion[index]);
    }

    const handleDataRecadacion = (datarec) => {
        var datatime = new Date();
        var dias = String(datatime.getDate()).padStart(2, '0');
        var mess = String(datatime.getMonth() + 1).padStart(2, '0');
        var anos = String(datatime.getFullYear());
        var fecha = anos + '-' + mess + '-' + dias;

        if (fecha > datarec) {
            setData(datarec);
            let ano = (datarec.substr(2, 2));
            let mes = (datarec.substr(5, 2));
            let dia = (datarec.substr(8, 2));

            let fechaRedux = dia + '/' + mes + '/' + ano;

            let token = localStorage.getItem("token");
            let config = {
                headers: {
                    Authorization: 'Bearer ' + token
                }
            }
            api.get('/ventas-useeffect/' + fechaRedux, config).then((res) => {

                setCaja(res.data[0]);
                setValReca(res.data[0].venta);
                setRecadacion(res.data);
                setLoading(false)
            })
        }
        else {
            alert("No se puede elegir una fecha posterior a hoy");
        }
    }

    const [loadingCaja, setLoadingCaja] = React.useState(false);

    const handelEntrega = async () => {
        setLoadingCaja(true);

        let token = localStorage.getItem("token");
        let config = {
            headers: {
                Authorization: 'Bearer ' + token
            }
        }


        let index = recadacion.findIndex(object => {
            return object.venta === valueReca;
        });

        var update = recadacion;

        let res = await api.post('ventas-update', { data: update[index], value: parseInt(valuSaldoAdd) }, config).then((res) => {

            console.log(res.data);

            update[index].saldo -= parseInt(valuSaldoAdd);
            update[index].entrega += parseInt(valuSaldoAdd);



            setRecadacion(update);
            setCaja(update[index]);

        })


        setLoadingCaja(false);


    }

    const [ventaDesde, setDesde] = React.useState([]);
    const [ventaHasta, setHasta] = React.useState([]);
    return (
        <>
            {loading
                ?
                (<></>)
                :
                (
                    <Box direction='row' gap='small'>
                        <Sdbr visible={visibleSidebar} position="right" className="p-sidebar-sm" onHide={() => setVisibleSidebar(false)}></Sdbr>
                        <Sidebar style={styles.sidebar} round="small"
                            // eslint-disable-next-line jsx-a11y/alt-text
                            header={<img style={{ width: '25vh' }} src={logo} />}
                            footer={
                                // <Button icon={<Icons.Help />} hoverIndicator />
                                <Text size='small'> Powerd by: busticket.cl </Text>
                            }
                        >
                            <Nav gap="small" style={{ paddingLeft: '2vh' }}>
                                <Button style={styles.navButtonActive}   > <Home /> <Text style={{ paddingLeft: '2vh' }} >Dashboard </Text> </Button>
                                <Button style={styles.navButtonInactive} > <BarChart />   <Text style={{ paddingLeft: '2vh' }}> Ventas  </Text></Button>
                                <Button style={styles.navButtonInactive} > <Clipboard /> <Text style={{ paddingLeft: '2vh' }}>Factuarion</Text> </Button>
                                <Button style={styles.navButtonInactive} > <Group /><Text style={{ paddingLeft: '2vh' }}>Usuarios</Text> </Button>
                                <Button style={styles.navButtonInactive}> <SettingsOption /><Text style={{ paddingLeft: '2vh' }}>Ajustes</Text> </Button>
                            </Nav>
                        </Sidebar>
                        <Box style={{ width: '100%' }}>
                            <Header pad="small">


                                <Box direction={'row'} spacing={2} alignItems={'center'} fill={'true'}>
                                    {/* <TextField id="outlined-basic" variant="outlined" style={{ width: '95%', border: '0px' }} /> */}
                                    <TextInput
                                        placeholder="Procurar..."

                                        value={serachInput}
                                        onChange={event => setSearchInput(event.target.value)}
                                        style={{
                                            width: '95%', border: 'none',
                                            '&:focus': {
                                                boxShadow: 'none',
                                                borderColor: 'red',
                                            }
                                        }}
                                        icon={<Search />}
                                    />

                                    <Avatar {...stringAvatar(user.nome)} />

                                    <Button>
                                        <KeyboardArrowDownIcon />
                                    </Button>

                                    <Text>  {user.nome}</Text>
                                </Box>
                            </Header>

                            <Box
                                pad={{
                                    top: "xlarge",
                                    left: "small",
                                    right: "medium"
                                }}
                                background={'#F4F7FC'}
                                fill={true}
                                gap={'large'}
                            >
                                <Box pad={{
                                    left: "xlarge",

                                }}>
                                    <Box gap='small' >
                                        <Text style={{ fontWeight: 'bold', fontFamily: 'Poppins', fontSize: 30 }}> CAJA</Text>
                                        <Box pad="large" background={'white'} style={{ borderRadius: '5%' }}  >

                                            <Box background={'white'} direction="row" gap={'large'} >
                                                <Text style={{ fontWeight: 'bold', fontFamily: 'Poppins', fontSize: 30 }}> Reacudacin</Text>





                                                <Select
                                                    labelKey="venta"
                                                    valueKey={{ key: 'venta', reduce: true }}
                                                    value={valueReca}
                                                    options={recadacion}
                                                    onChange={({ value: nextValue }) => { setValReca(nextValue); handleSelectRecadacion(nextValue); }}
                                                    size={'small'}
                                                />
                                                <Box width={'20%'}>
                                                    <DateInput
                                                        format="dd/mm/yyyy"
                                                        value={data}
                                                        onChange={({ value }) => { handleDataRecadacion(value); }}
                                                        size={'small'}
                                                    />
                                                </Box>
                                                <Box direction='row' gap='small'>
                                                    <TextInput
                                                        type={'number'}
                                                        placeholder="Entrega"
                                                        style={{ width: '80%' }}
                                                        value={valuSaldoAdd}
                                                        onChange={event => setSaldoAdd(event.target.value)}
                                                        size={'small'}
                                                    />

                                                    <Button primary style={{ width: '120%', justifyContent: 'center', alignContent: 'center', display: 'flex', alignItems: 'center' }} onClick={() => handelEntrega()} >

                                                        <Text> Agregar saldo </Text>

                                                    </Button>
                                                </Box>
                                            </Box>
                                            {loadingCaja
                                                ?
                                                (<Box style={{ justifyContent: 'center', alignContent: 'center', display: 'flex', alignItems: 'center', marginTop: '2%' }}>
                                                    <Spinner />
                                                </Box>
                                                )
                                                :
                                                (
                                                    <DataTable
                                                        background={'white'}
                                                        columns={[
                                                            {
                                                                property: 'total',
                                                                header: <Text>TOTAL</Text>,
                                                                primary: true,
                                                            },
                                                            {
                                                                property: 'tarjeta',
                                                                header: <Text>TARJETA</Text>,
                                                            },
                                                            {
                                                                property: 'efectivo',
                                                                header: <Text>EFECTIVO</Text>,
                                                            },
                                                            {
                                                                property: 'entrega',
                                                                header: <Text>ENTREGA</Text>,
                                                            },
                                                            {
                                                                property: 'saldo',
                                                                header: <Text>SALDO</Text>,
                                                            },
                                                        ]}
                                                        data={[
                                                            {
                                                                total: 'L. ' + parseInt(caja.total).toLocaleString('pt-br', { currency: 'CLP' }) + '.00',
                                                                tarjeta: 'L. ' + parseInt(caja.tarjeta).toLocaleString('pt-br', { currency: 'CLP' }) + '.00',
                                                                efectivo: 'L. ' + parseInt(caja.efectivo).toLocaleString('pt-br', { currency: 'CLP' }) + '.00',
                                                                entrega: 'L. ' + parseInt(caja.entrega).toLocaleString('pt-br', { currency: 'CLP' }) + '.00',
                                                                saldo: 'L. ' + parseInt(caja.saldo).toLocaleString('pt-br', { currency: 'CLP' }) + '.00',
                                                            },

                                                        ]}
                                                    />
                                                )}
                                        </Box>
                                    </Box>
                                </Box>


                                <Box gap='small' >
                                    <Text style={{ fontWeight: 'bold', fontFamily: 'Poppins', fontSize: 30 }}> VENTAS</Text>
                                    <Box pad="large" background={'white'} style={{ borderRadius: '5%' }}  >

                                        <Box background={'white'} direction="row" gap={'large'} >
                                            <Text style={{ fontWeight: 'bold', fontFamily: 'Poppins', fontSize: 30 }}> Detalle de ventas</Text>

                                            <Text style={{ fontWeight: 'bold', fontFamily: 'Poppins', fontSize: 30 }}> Desde:</Text>
                                            <Box width={'20%'}>
                                                <DateInput
                                                    format="dd/mm/yyyy"
                                                    value={(new Date()).toISOString()}
                                                    onChange={({ value }) => { }}
                                                    size={'small'}
                                                />
                                            </Box>
                                            <Text style={{ fontWeight: 'bold', fontFamily: 'Poppins', fontSize: 30 }}> Hasta:</Text>
                                            <Box width={'20%'}>
                                                <DateInput
                                                    format="dd/mm/yyyy"
                                                    value={(new Date()).toISOString()}
                                                    onChange={({ value }) => { }}
                                                    size={'small'}
                                                />
                                            </Box>
                                        </Box>
                                        <DataTable
                                            background={'white'}
                                            columns={[
                                                {
                                                    property: 'vendedor',
                                                    header: <Text>Vendedor</Text>,
                                                    primary: true,
                                                },
                                                {
                                                    property: 'origen',
                                                    header: <Text>ORIGEN</Text>,
                                                },
                                                {
                                                    property: 'destino',
                                                    header: <Text>DESTINO</Text>,
                                                },
                                                {
                                                    property: 'cantidad',
                                                    header: <Text>CANTIDAD</Text>,
                                                },
                                                {
                                                    property: 'precio',
                                                    header: <Text>PRECIO</Text>,
                                                },
                                                {
                                                    property: 'factura',
                                                    header: <Text>FACTURA</Text>,
                                                },
                                                {
                                                    property: 'hora',
                                                    header: <Text>HORA</Text>,
                                                },
                                                {
                                                    property: 'fecha',
                                                    header: <Text>Fecha</Text>,
                                                },
                                                {
                                                    property: 'documento',
                                                    header: <Text>RTN-ID CLIENTE</Text>,
                                                },
                                            ]}
                                            data={detallesVendas}
                                        />
                                    </Box>
                                </Box>


                            </Box>
                        </Box>
                    </Box>
                )
            }
        </>
    )



};

export default AdminScreen;